.avatarRoot {
    position: relative;
    display: flex;
    height: 2.5rem;
    width: 2.5rem;
    shrink: 0;
    overflow: hidden;
    border-radius: 9999px;
  }
  
  .avatarImage {
    aspect-ratio: 1 / 1;
    height: 100%;
    width: 100%;
  }
  
  .avatarFallback {
    display: flex;
    height: 100%;
    width: 100%;
    align-items: center;
    justify-content: center;
    border-radius: 9999px;
    background-color: #e5e5e5;
  }